import React, { useState, useEffect, useRef, useCallback } from "react";
import { Modal } from "react-bootstrap";
import { resendOtpslogin } from "../../../../api/login/Login";
import {
  toastErrorMessage,
  toastSuccessMessage,
} from "../../../compeleteRegister/ToastShare";

const MobilesinupOtpModal = ({
  show,
  onHide,
  otp,
  inputs,
  otpMessage,
  otpError,
  handleResendOTP,
  otpHandleChange,
  handleKeyDown,
  handleOtpSubmit,
  setOtp,
  // resendOtpslogin, // Assuming you have an API function for resending OTP
  storedMobileNo,
  logintoken,
}) => {
  const [isTimerEnded, setIsTimerEnded] = useState(false);
  const [timeLeft, setTimeLeft] = useState(120);
  const [resendLoading, setResendLoading] = useState(false);
  const [isResendDisabled, setIsResendDisabled] = useState(true);

  const timerRef = useRef(null);

  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes.toString().padStart(2, "0")}:${remainingSeconds
      .toString()
      .padStart(2, "0")}`;
  };

  const startTimer = useCallback(() => {
    if (timerRef.current) clearInterval(timerRef.current);

    setTimeLeft(120);
    setIsTimerEnded(false);
    setIsResendDisabled(true);

    timerRef.current = setInterval(() => {
      setTimeLeft((prevTime) => {
        if (prevTime <= 1) {
          clearInterval(timerRef.current);
          setIsResendDisabled(false);
          setIsTimerEnded(true);
          return 0;
        }
        return prevTime - 1;
      });
    }, 1000);
  }, []);

  useEffect(() => {
    if (!show) return;
    startTimer();
    return () => {
      if (timerRef.current) clearInterval(timerRef.current);
    };
  }, [show, startTimer]);

  useEffect(() => {
    if (show) {
      setTimeout(() => {
        if (inputs.current && inputs.current[0]) {
          inputs.current[0].focus();
        }
      }, 300);
    }
  }, [show]);

  const handleResendOtp = useCallback(async () => {
    try {
      setResendLoading(true);
      setIsResendDisabled(true);
      const response = await resendOtpslogin({
        customer_mobile: storedMobileNo,
        user_id: logintoken,
      });
      // console.log("response", response);
      if (response?.data?.statusCode == 200) {
        toastSuccessMessage(response?.data?.message);
        setOtp(new Array(6).fill(""));
        startTimer();
        setTimeout(() => {
        if (inputs.current && inputs.current[0]) {
          inputs.current[0].focus();
        }
      }, 300);
      } else {
        toastErrorMessage(response?.data?.data?.message);
      }
    } catch (error) {
      console.error("Error resending OTP:", error);
    } finally {
      setResendLoading(false);
      setIsResendDisabled(false);
    }
  }, [startTimer, setOtp, resendOtpslogin, storedMobileNo, logintoken]);

  return (
    <Modal
      show={show}
      onHide={onHide}
      centered
      backdrop="static"
      keyboard={false}
      className="email-optp-modle"
      style={{ zIndex: 1050 }}
    >
      <Modal.Body
        style={{
          backgroundColor: `var(--bs-primary1)`,
          color: `var(--bs-primary2)`,
          border: "2px solid var(--bs-primary3)",
          transform: "translateZ(0)",
        }}
      >
        <button
          onClick={onHide}
          className="otp-close-btn"
          style={{
            position: "absolute",
            top: "10px",
            right: "10px",
            background: "transparent",
            border: "none",
            fontSize: "22px",
            color: "#fff",
            cursor: "pointer",
            zIndex: 2000,
          }}
        >
          âœ•
        </button>
        <div className="set-head">
          <div className="row">
            <div className="col-lg-12">
              <div className="headinf-otp">
                <h2>Mobile OTP Verification</h2>
                <p className="set-lage">
                  Enter the 6-digit verification code sent to your mobile.
                </p>
              </div>
              {isTimerEnded ? (
                <div className="d-flex justify-content-center align-items-center">
                  <button
                    type="button"
                    className="btn-login"
                    onClick={handleResendOtp}
                    disabled={resendLoading || isResendDisabled}
                  >
                    {resendLoading ? (
                      <span
                        className="spinner-border spinner-border-sm"
                        role="status"
                        aria-hidden="true"
                      ></span>
                    ) : (
                      "Resend OTP"
                    )}
                  </button>
                </div>
              ) : (
                <>
                  <div className="d-flex justify-content-between align-items-center">
                    <span>OTP expires in {formatTime(timeLeft)}</span>
                    <span
                      className="resend-otp"
                      onClick={handleResendOtp}
                      style={{
                        cursor: isResendDisabled ? "default" : "pointer",
                        color: isResendDisabled ? "gray" : "blue",
                        pointerEvents: isResendDisabled ? "none" : "auto",
                      }}
                    >
                      Resend OTP
                    </span>
                  </div>

                  <div className="otp-inputs">
                    {otp.map((value, index) => (
                      <input
                        key={index}
                        type="number"
                        maxLength="1"
                        value={value}
                        onChange={(e) =>
                          otpHandleChange(index, e.target.value, setOtp, inputs)
                        }
                        onKeyDown={(e) => handleKeyDown(index, e, inputs)}
                        ref={(input) => (inputs.current[index] = input)}
                        className="otp-input"
                      />
                    ))}
                  </div>
                  <div className="d-flex justify-content-center align-items-center">
                    <button
                      type="button"
                      className="btn btn-login"
                      onClick={() => handleOtpSubmit("mobile")}
                      disabled={
                        resendLoading || otp.some((digit) => digit === "")
                      }
                    >
                      {resendLoading ? (
                        <span
                          className="spinner-border spinner-border-sm"
                          role="status"
                          aria-hidden="true"
                        ></span>
                      ) : (
                        "Submit OTP"
                      )}
                    </button>
                  </div>
                </>
              )}
              {otpMessage && (
                <div className="alert alert-success mt-3">{otpMessage}</div>
              )}
              {otpError && (
                <div className="alert alert-danger mt-3">{otpError}</div>
              )}
            </div>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default MobilesinupOtpModal;
