import { FaMobileScreenButton } from "react-icons/fa6";
import { FaRegUser } from "react-icons/fa";
import { MdOutlineEmail } from "react-icons/md";
import { RiLockPasswordFill } from "react-icons/ri";
import { MdEmail } from "react-icons/md";
import { TiTick } from "react-icons/ti";
import { useEffect, useRef, useState } from "react";
import TabSignUp from "../tabSinUp/TabSignUp";
import {
  CountryList,
  emailSinup,
  emailSinupOtp,
  isEmailExits,
  isEmailExits2,
  isVerifiedMobileOtp,
  registerUser,
  userType,
  resendOtpslogin,
} from "../../../../api/login/Login";
import { getUserDetails } from "../../../../utils/localStorage";
import { toast } from "react-toastify";
import { Link, useNavigate } from "react-router-dom";
import { Button, Modal } from "react-bootstrap";
import { MdVerified } from "react-icons/md";
import { onboardingRequest } from "../../../../api/login/Login";
import { FaMapMarkerAlt } from "react-icons/fa";
import axios from "axios";
import { baseUrl } from "../../../../baseUrl";
import {
  toastErrorMessage,
  toastSuccessMessage,
} from "../../../compeleteRegister/ToastShare";
import NewTabSignUp from "../tabSinUp/NewTabSignUp";
import MobilesinupOtpModal from "./MobilesinupOtpModal";
function SignUpMerchantForm({
  initalValue,
  handleChange,
  mobileGenerateOtpMobile,
  userIDD,
  mobileVeridedInput,
  getOtp,
  mobileOtp,
  emailVeridedInput,
  emailgetOtp,
  nextForm,
  emailOtp,
  submitOtpEmail,
  formResiter,
  setEmailVeridedInput,
  setMobileOtp,
  setMobileVerified,
  handleCountryCode,
  countryCode,
  setEmailOtp,
  setFormResiter,
  loader1,
  showMobileOtp,
  handleClose2,
  mobileExit,
  setMobileExit,
  veriFiedIconMobile,
  setVeriFiedIconMobile,
}) {
  // console.log(countryCode);
  // console.log(initalValue.mobileNo.length);

  const [showEmailOtp, setShowEmailOtp] = useState(false);
  const timerRef = useRef(null);

  const [emailExit, setemailExit] = useState("");

  const [veriFiedIconEmail, setVeriFiedIconEmail] = useState(false);
  const [isCheckboxDisabled, setIsCheckboxDisabled] = useState(true);

  const handleClose3 = () => setShowEmailOtp(false);
  const handleShow3 = () => setShowEmailOtp(true);

  const [isChecked, setIsChecked] = useState(false);

  const [show, setShow] = useState(false);

  const handleClose = () => {
    setIsChecked(false);
    setShow(false);
    // setMobileVerified(true)
    // setMobileOtp(false)
    // setFormResiter(false)
  };
  const handleShow = () => {
    setShow(true);
  };

  const [emailUser, setEmailUser] = useState();
  const [loader2, setloader2] = useState(false);
  const [loader3, setloader3] = useState(false);
  const [loader4, setloader4] = useState(false);
  const [loader5, setloader5] = useState(false);
  const navigate = useNavigate();
  const [data, setData] = useState(null);
  const [country, setCountry] = useState(null);
  // console.log(country);

  const [userId, setUserId] = useState();
  // console.log(userId);
  const [mobileId, setMobile] = useState();
  // console.log(mobileId);

  const [errorValue, setErrorValue] = useState({});

  const [count, setCount] = useState(0);
  const [stateList, setStateList] = useState([]);
  const [selectedState, setSelectedState] = useState("");
  const [searchQuery, setSearchQuery] = useState("");

  const [isTimerEnded, setIsTimerEnded] = useState(false);
  const [timeLeft, setTimeLeft] = useState(180);
  const [resendLoading, setResendLoading] = useState(false);
  const [loading, setLoading] = useState(false);
  const [message2, setMessage2] = useState("");
  const [error2, setError2] = useState("");
  const [otpMessage, setOtpMessage] = useState("");
  const [otpError, setOtpError] = useState("");
  const [selectedUserType, setSelectedUserType] = useState("Super Distributor");

  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes.toString().padStart(2, "0")}:${remainingSeconds
      .toString()
      .padStart(2, "0")}`;
  };
  const [stateOptions, setStateOptions] = useState([]);
  const length = 6;
  const [otp, setOtp] = useState(new Array(length).fill(""));
  const [otp1, setOtp1] = useState(new Array(length).fill(""));
  const inputs = useRef([]);
  const inputs1 = useRef([]);
  const [step, setStep] = useState(1);

  const nextStep = () => setStep(step + 1);
  const prevStep = () => setStep(step - 1);

  // console.log(initalValue.mobileNo.slice(3, 13));
  // const user_id=data?.[0]?._id :''

  const user_idcus = data?.[0]?._id || "65f3fb87a6a2a92f979b47eb";
  const [resiter, setResister] = useState({
    email: "",
    mobile: mobileId,
    user_type_id: user_idcus,
    name: "",
    password: "",
    refer_id: "",
    domain_name: "",
  });
  console.log("register", resiter);

  const validation = (values) => {
    const error = {};
    const regexEmail = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;
    if (!isNameValid(values.name)) {
      error.name = "Name is Required!";
    }

    // if (!values.email) {
    //     error.email = "Email is required";
    // } else if (!regexEmail.test(values.email)) {
    //     error.email = "Invalid Email";
    // }
    if (!values.password) {
      error.password = "Password is Required!";
    }
    if (!values.refer_id) {
      error.refer_id = "Refer Id Required!";
    }
    if (!selectedState) {
      error.state = "State is Required!";
    }

    if (
      ["reseller partner api user", "Api User", "Reseller"].includes(
        selectedUserType,
      ) &&
      !values?.domain_name
    ) {
      error.domain_name = "Domain name is required!";
    }

    return error;
  };

  // console.log(otp);

  let str = "";
  for (let index = 0; index < otp.length; index++) {
    const element = otp[index];
    str = str + element;
  }
  const handleKeyPress = (event) => {
    const cursorPosition = event.target.selectionStart;
    if (event.ctrlKey && event.key === "Backspace") {
      event.preventDefault();
      return;
    }
    if (
      (event.key === "Backspace" || event.key === "Delete") &&
      cursorPosition <= 3
    ) {
      event.preventDefault();
      return;
    }
    if (event.key === "Enter") {
      getOtp();
    }
  };
  const handleKeyPress2 = (event) => {
    if (event.key === "Enter") {
      SubmitEmail();
    }
  };
  console.log(str);

  const otpChandleChange = (index, value) => {
    const newOtp = [...otp];
    if (value.length == 2) {
      return;
    }
    newOtp[index] = value;
    setOtp(newOtp);

    if (value && inputs.current[index + 1]) {
      inputs.current[index + 1].focus();
    }
  };

  const handleKeyDown = (index, e) => {
    if (e.key === "Backspace" && !otp[index] && inputs.current[index - 1]) {
      inputs.current[index - 1].focus();
    }
  };

  const handleEnterPress = (e) => {
    if (e.key === "Enter" && otp.every((value) => value.length === 1)) {
      submitOtp();
    }
  };

  // const otpChandleChange = (e) => {
  //     const clone = { ...otp }
  //     const vlaue = e.target.value
  //     const name = e.target.name
  //     clone[name] = vlaue
  //     setOtp(clone)
  // }

  const handleResiter = (e) => {
    const clone = { ...resiter };
    let value = e.target.value;
    const name = e.target.name;

    if (name === "name") {
      // Only allow letters and spaces for the name field
      value = value.replace(/[^a-zA-Z\s]/g, "");
    }

    clone[name] = value;
    setResister(clone);
  };

  const handleResiter2 = (e) => {
    const clone = { ...resiter, user_type_id: e };
    const selectedUser = data.find((item) => item._id === e);
    if (selectedUser) {
      setSelectedUserType(selectedUser.user_type);
    }
  };

  const toastSuccessMessage = (message) => {
    toast.success(`${message}`, {
      position: "top-center",
      autoClose: 2000,
    });
  };

  const toastSuccessMessage1 = (message) => {
    toast.error(`${message}`, {
      position: "top-center",
      autoClose: 2000,
    });
  };
  const resetOtpState = () => {
    setTimeLeft(180);
    setIsTimerEnded(false);
    setOtpMessage("");
    setOtpError("");
    setOtp(new Array(length).fill(""));
    setOtp1(new Array(length).fill(""));
  };

  const tabChange = (i, id) => {
    setUserId(id);
    const clone = { ...resiter, user_type_id: id };
    // console.log(clone);
    setResister(clone);
    setCount(i);
    const selectedUser = data.find((item) => item._id === id);
    if (selectedUser) {
      setSelectedUserType(selectedUser.user_type);
    }
  };

  const handlemobileotpclose = () => {
    handleClose2();

    setTimeLeft(180);
    setIsTimerEnded(false);
    setMessage2("");
    setError2("");
    setOtp(new Array(length).fill(""));
  };
  const submitOtp = async () => {
    // debugger
    setloader2(true);
    try {
      const res = await isVerifiedMobileOtp({ otp: str, user_id: userIDD });
      // console.log(res?.data?.data.user);
      setMobile(res?.data?.data?.user);
      if (res?.data?.statusCode == "200") {
        getUserDetails(res?.data?.user);
        toastSuccessMessage(res.data.data.message);
        // debugger
        setTimeout(() => {
          setEmailVeridedInput(true);
          setMobileOtp(false);
          setMobileVerified(false);
        }, 2000);
        handlemobileotpclose();
        setVeriFiedIconMobile(true);
        setMobileExit("");
      }
      setloader2(false);

      if (res?.data?.error == true) {
        toastSuccessMessage1(res?.data?.message);
      }
      // console.log(res);
    } catch (error) {
      setloader2(false);
    }
  };

  const handleSubmit = async (event) => {
    setloader5(true);
    const cloen = {
      ...resiter,
      mobile: mobileId,
      email: emailUser,
      state: selectedState,
    };
    console.log("cloen", cloen);

    event.preventDefault();
    setErrorValue(validation(cloen));

    try {
      const res = await registerUser(cloen);
      if (res?.statusCode == "200") {
        toastSuccessMessage("User created Successfully");

        setTimeout(() => {
          navigate("/login-area");
        }, 2000);
      }
      if (res?.statusCode == 402) {
        toastSuccessMessage1(res?.message);
      }
      setloader5(false);
    } catch (error) {
      setloader5(true);
      // console.log(error);
      // toastSuccessMessage(res?.data?.message)
    }
  };

  const [emailInitial, setEmailInitial] = useState({
    deviceId: "",
    emailId: "",
  });
  const isValidEmail = (email) => {
    const emailRegex =
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
    return emailRegex.test(email);
  };

  const handleEmail = (e) => {
    const value = e.target.value;
    const name = e.target.name;

    // Update emailInitial state
    const cloneEmail = { ...emailInitial };
    cloneEmail[name] = value;
    setEmailInitial(cloneEmail);

    // Update emailUser state
    setEmailUser(value);

    if (value.trim() === "") {
      setemailExit("");
      setVeriFiedIconEmail(false);
      setErrorMessageEmail(""); // Clear the error message when input is empty
    } else {
      // Clear the "Valid email field is required" message when user starts typing
      setErrorMessageEmail("");
    }

    // You might want to add basic email validation here
    // and only call isEmailExitt if the email looks valid
    if (isValidEmail(value)) {
      isEmailExitt(value);
    }
  };

  const [debouncedValue, setDebouncedValue] = useState(emailInitial.emailId);

  const [errorMessageEmail, setErrorMessageEmail] = useState();

  const isEmailExitt = async (value) => {
    try {
      const res = await isEmailExits2(value);
      if (res?.data?.isExist == true) {
        setErrorMessageEmail("");
        setemailExit("Email ID Already Exists");
      }
      if (res?.data?.isExist == false) {
        setemailExit("");
        setErrorMessageEmail("");
        SubmitEmail();
      }
    } catch (error) {}
  };
  const isNameValid = (name) => {
    return name.trim().length > 0;
  };

  const SubmitEmail = async () => {
    setloader3(true);
    try {
      const res = await emailSinup(emailInitial);

      if (res?.data?.error === false) {
        window.localStorage.setItem("emailToken", res?.data?.data.user);
        setEmailVeridedInput(false);
        setEmailOtp(true);
        setErrorMessageEmail("");
      }

      if (res?.data?.error === true) {
        setErrorMessageEmail(res?.data?.message);
      }
      setloader3(false);
    } catch (error) {
      setloader3(false);
    }
  };

  const length1 = 6;

  // console.log(otp);

  let str1 = "";
  for (let index = 0; index < otp1.length; index++) {
    const element = otp1[index];
    str1 = str1 + element;
  }

  console.log(str1);

  const otpChandleChange1 = (index, value) => {
    const newOtp = [...otp1];
    if (value.length == 2) {
      return;
    }
    newOtp[index] = value;
    setOtp1(newOtp);

    if (value && inputs1.current[index + 1]) {
      inputs1.current[index + 1].focus();
    }
  };

  const handleEnterPressEmail = (e) => {
    if (e.key === "Enter" && otp1.every((value) => value.length === 1)) {
      submitEmailOtp();
    }
  };

  const submitEmailOtp = async () => {
    setloader4(true);
    const otpEmailData = {
      otp: str1,
      user_id: window.localStorage.getItem("emailToken"),
    };
    try {
      const res = await emailSinupOtp(otpEmailData);
      setEmailUser(res?.data?.data.user);
      // console.log(res?.data?.data.user);
      if (res.data.error == false) {
        toastSuccessMessage(res?.data?.message);
        setVeriFiedIconEmail(true);
        setemailExit("");
        setFormResiter(true);
        setShowEmailOtp(false);
        setEmailVeridedInput(false);
        setEmailOtp(false);
      }
      if (res.data.error == true) {
        toastSuccessMessage1(res?.data?.message);
      }
      setloader4(false);
    } catch (error) {
      setloader4(false);
    }
  };
  const inputRef = useRef(null);

  const handlePaste = (event) => {
    event.preventDefault();
  };

  const handleMouseDown = (event) => {
    if (document.activeElement === inputRef.current) {
      event.preventDefault();
    }
  };

  const handleSelect = (event) => {
    const input = inputRef.current;
    if (input) {
      const start = input.selectionStart;
      const end = input.selectionEnd;

      if (start < 3) {
        event.preventDefault();
        if (end > 3) {
          input.setSelectionRange(3, end);
        } else {
          input.setSelectionRange(3, 3);
        }
      }
    }
  };

  const handleFocus = (event) => {
    if (inputRef.current) {
      inputRef.current.setSelectionRange(3, inputRef.current.value.length);
    }
  };
  const handleCheckboxChange = (event) => {
    setIsChecked(event.target.checked);
    // handleShow();
  };

  const handleStateChange = (e) => {
    setSelectedState(e.target.value);
  };
  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
  };

  const [location, setLocation] = useState({ latitude: null, longitude: null });
  const [error, setError] = useState(null);

  const sendOnboardingRequest = async () => {
    let currentLocation = { ...location };

    // If location is not set, try to get it one more time
    if (!currentLocation.latitude || !currentLocation.longitude) {
      await new Promise((resolve) => {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            currentLocation = {
              latitude: position.coords.latitude,
              longitude: position.coords.longitude,
            };
            setLocation(currentLocation);
            resolve();
          },
          () => {
            console.warn("Unable to get location.");
            resolve();
          },
          { timeout: 5000, maximumAge: 0 },
        );
      });
    }

    const onboardingData = {
      name: resiter.name,
      email: emailInitial.emailId,
      mobileNo: initalValue.mobileNo,
      state: selectedState,
    };

    if (currentLocation.latitude && currentLocation.longitude) {
      onboardingData.latitude = currentLocation.latitude;
      onboardingData.longitude = currentLocation.longitude;
    }

    try {
      const response = await onboardingRequest(onboardingData);
      console.log("Onboarding request sent:", response);
    } catch (error) {
      console.error("Error sending onboarding request:", error);
    }
  };

  const disabledButtonStyle = {
    cursor: "none",
    backgroundColor: "#d3d3d3",
  };

  const handleResendOTP = async (type) => {
    setResendLoading(true);
    setOtpError("");
    setOtpMessage("");

    try {
      let response;
      // if (type === 'mobile') {
      response = await resendOtpslogin({ user_id: userIDD });
      // } else {
      //     // Assuming you have a similar function for email OTP resend
      //     response = await resendEmailOtp({user_id: window.localStorage.getItem('emailToken')});
      // }

      if (response.status === 200) {
        setOtpMessage("OTP has been resent successfully.");
        resetOtpState();
      } else {
        setOtpError("Failed to resend OTP.");
      }
    } catch (error) {
      setOtpError("Failed to resend OTP.");
    } finally {
      setResendLoading(false);
    }
  };
  const otpHandleChange = (index, value, setOtpFunction, inputsRef) => {
    if (value.length > 1) return;
    const newOtp = [...(setOtpFunction === setOtp ? otp : otp1)];
    newOtp[index] = value;
    setOtpFunction(newOtp);

    if (value && inputsRef.current[index + 1]) {
      inputsRef.current[index + 1].focus();
    }
  };
  const handleOtpSubmit = async (type) => {
    setResendLoading(true);
    setOtpError("");
    setOtpMessage("");

    try {
      let response;
      if (type === "mobile") {
        response = await isVerifiedMobileOtp({
          otp: otp.join(""),
          user_id: userIDD,
        });
      } else {
        response = await emailSinupOtp({
          otp: otp1.join(""),
          user_id: window.localStorage.getItem("emailToken"),
        });
      }

      if (response.data.error === false) {
        setOtpMessage(response.data.message);
        if (type === "mobile") {
          setMobile(response.data.data.user);
          setEmailVeridedInput(true);
          setMobileOtp(false);
          setMobileVerified(false);
          setVeriFiedIconMobile(true);
          setMobileExit("");
        } else {
          setEmailUser(response.data.data.user);
          setVeriFiedIconEmail(true);
          setemailExit("");
          setFormResiter(true);
          setShowEmailOtp(false);
          setEmailVeridedInput(false);
          setEmailOtp(false);
        }
        handleCloseOtpModal(type);
      } else {
        setOtpError(response.data.message);
      }
    } catch (error) {
      setOtpError("Failed to verify OTP.");
    } finally {
      setResendLoading(false);
    }
  };

  const handleCloseOtpModal = (type) => {
    if (type === "mobile") {
      handleClose2();
    } else {
      setShowEmailOtp(false);
    }
    resetOtpState();
  };
  useEffect(() => {
    const fetchInitialData = async () => {
      try {
        const [countryRes, userTypeRes] = await Promise.all([
          CountryList(),
          userType(),
        ]);
        setCountry(countryRes?.data);
        setData(userTypeRes?.data);
        setUserId(userTypeRes?.data?.[0]?._id);
      } catch (error) {
        console.error("Error fetching initial data:", error);
      }
    };
    const fetchStates = async () => {
      try {
        const response = await axios.get(`${baseUrl}state/country/101`);
        if (response.data.error === false) {
          const states = response.data.data.map((state) => ({
            value: state._id,
            name: state.name,
          }));
          setStateOptions(states);
        } else {
          console.error("Error fetching states:", response.data.message);
        }
      } catch (error) {
        console.error("Error fetching states:", error);
      }
    };

    const getGeolocation = () => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            setLocation({
              latitude: position.coords.latitude,
              longitude: position.coords.longitude,
            });
            console.log("location", location);
          },
          (error) => {
            setError(error.message);
            console.error("Geolocation error:", error.message);
          },
        );
      } else {
        toastErrorMessage("Geolocation is not supported by this browser.");
        console.error("Geolocation is not supported by this browser.");
      }
    };

    fetchInitialData();
    fetchStates();
    getGeolocation();
  }, []);

  useEffect(() => {
    setResister((prev) => ({
      ...prev,
      password: initalValue.mobileNo.slice(3, 13),
    }));
  }, [initalValue.mobileNo]);

  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(emailInitial.emailId);
    }, 2000);

    return () => clearTimeout(handler);
  }, [emailInitial.emailId]);

  useEffect(() => {
    if (debouncedValue) {
      isEmailExitt(debouncedValue);
    }
  }, [debouncedValue]);

  useEffect(() => {
    if (isChecked) {
      sendOnboardingRequest();
    }
  }, [isChecked]);

  useEffect(() => {
    const areFieldsFilled =
      resiter.name &&
      selectedState &&
      initalValue.mobileNo.length === 13 &&
      emailInitial.emailId;
    setIsCheckboxDisabled(!areFieldsFilled);
  }, [resiter.name, selectedState, initalValue.mobileNo, emailInitial.emailId]);

  useEffect(() => {
    setOtp(new Array(length).fill(""));
  }, [showMobileOtp]);

  useEffect(() => {
    setOtp1(new Array(length).fill(""));
  }, [showEmailOtp]);
  console.log("selectedUserType", selectedUserType);
  const showNothing = ["Super Distributor"];
  const showBoth = ["Distributor", "Retailer"];
  const showReferOnly = ["Api User", "Reseller", "reseller partner api user"];
  const mustHaveReferId = showReferOnly.includes(selectedUserType);

  const mustChooseReferOrCheckbox = showBoth.includes(selectedUserType);

  const isReferRequirementFailed =
    (mustHaveReferId && !resiter.refer_id) ||
    (mustChooseReferOrCheckbox && !resiter.refer_id && !isChecked);

  return (
    <>
      <div className="my-3">
        <MobilesinupOtpModal
          show={showMobileOtp}
          onHide={() => handleCloseOtpModal("mobile")}
          isTimerEnded={isTimerEnded}
          resendLoading={resendLoading}
          timeLeft={timeLeft}
          otp={otp}
          inputs={inputs}
          otpMessage={otpMessage}
          otpError={otpError}
          handleResendOtp={handleResendOTP}
          formatTime={formatTime}
          otpHandleChange={otpHandleChange}
          handleKeyDown={handleKeyDown}
          handleOtpSubmit={handleOtpSubmit}
          setOtp={setOtp}
          logintoken={userIDD}
        />
        <Modal
          show={showEmailOtp}
          onHide={() => handleCloseOtpModal("email")}
          centered
          className="email-optp-modle"
          style={{
            zIndex: 1050,
          }}
        >
          <Modal.Body
            style={{
              backgroundColor: `var(--bs-primary1)`,
              color: `var(--bs-primary2)`,
              border: "2px solid var(--bs-primary3)",
              transform: "translateZ(0)",
            }}
          >
            <div className="set-head">
              <div className="row">
                <div className="col-lg-12">
                  <div className="headinf-otp">
                    <h2>Email OTP Verification</h2>
                    <p className="set-lage">
                      Enter 6 Digit Number Verification Code On Your Email ID!
                    </p>
                  </div>
                  {isTimerEnded ? (
                    <div className="d-flex justify-content-center align-items-center">
                      <button
                        type="button"
                        className="btn btn-resend-otp"
                        onClick={() => handleResendOTP("email")}
                        disabled={resendLoading}
                      >
                        {resendLoading ? (
                          <span
                            className="spinner-border spinner-border-sm"
                            role="status"
                            aria-hidden="true"
                          ></span>
                        ) : (
                          "Resend OTP"
                        )}
                      </button>
                    </div>
                  ) : (
                    <>
                      <div className="verification-timer">
                        OTP expires in {formatTime(timeLeft)}{" "}
                        <span
                          className="resend-otp"
                          onClick={() => handleResendOTP("email")}
                        >
                          Resend OTP
                        </span>
                      </div>
                      <div className="otp-inputs">
                        {otp1.map((value, index) => (
                          <input
                            key={index}
                            type="number"
                            maxLength="1"
                            value={value}
                            onChange={(e) =>
                              otpHandleChange(
                                index,
                                e.target.value,
                                setOtp1,
                                inputs1,
                              )
                            }
                            onKeyDown={(e) => handleKeyDown(index, e, inputs1)}
                            ref={(input) => (inputs1.current[index] = input)}
                            className="otp-input"
                          />
                        ))}
                      </div>
                      <div className="d-flex justify-content-center align-items-center">
                        <button
                          type="button"
                          className="btn btn-submit-otp"
                          onClick={() => handleOtpSubmit("email")}
                          disabled={
                            resendLoading || otp1.some((digit) => digit === "")
                          }
                        >
                          {resendLoading ? (
                            <span
                              className="spinner-border spinner-border-sm"
                              role="status"
                              aria-hidden="true"
                            ></span>
                          ) : (
                            "Submit OTP"
                          )}
                        </button>
                      </div>
                    </>
                  )}
                  {otpMessage && (
                    <div className="alert alert-success mt-3">{otpMessage}</div>
                  )}
                  {otpError && (
                    <div className="alert alert-danger mt-3">{otpError}</div>
                  )}
                </div>
              </div>
            </div>
          </Modal.Body>
        </Modal>
        {formResiter ? (
          <form>
            <div className="row">
              <div className="col-12 mb-2">
                <div className="input-group ">
                  <span className="input-group-text" id="basic-addon1">
                    <FaRegUser />
                  </span>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter Your Name"
                    name="name"
                    value={resiter.name}
                    onChange={handleResiter}
                  />
                </div>
                <p style={{ color: "red", marginBottom: "2px" }}>
                  {errorValue.name}
                </p>
              </div>

              <div className="col-12 mb-2 ">
                {/* <label htmlFor="">Mobile <span style={{ color: 'red' }}>*</span></label> */}
                <div className="input-group input-group-set-mob">
                  <span className="input-group-text" id="basic-addon1">
                    <FaMobileScreenButton />
                  </span>

                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter Mobile Number"
                    ref={inputRef}
                    onMouseDown={handleMouseDown}
                    onSelect={handleSelect}
                    onPaste={handlePaste}
                    name="mobileNo"
                    onKeyDown={handleKeyPress}
                    value={initalValue.mobileNo}
                    onChange={handleChange}
                  />
                  {veriFiedIconMobile && (
                    <div className="setVerufied">
                      <MdVerified />
                    </div>
                  )}
                </div>
                {mobileExit && <p className="text-danger">{mobileExit}</p>}
              </div>
              <div className="col-12 mb-2 ">
                <div className="input-group input-group-set-mob">
                  <span className="input-group-text" id="basic-addon1">
                    <MdEmail />
                  </span>
                  <input
                    type="email"
                    className="form-control"
                    placeholder="Enter Email"
                    name="emailId"
                    onKeyDown={handleKeyPress2}
                    value={emailInitial.emailId}
                    onChange={handleEmail}
                    autocomplete="new-email"
                  />
                  {veriFiedIconEmail && (
                    <div className="setVerufied">
                      <MdVerified />
                    </div>
                  )}
                </div>
                {errorMessageEmail && (
                  <p style={{ color: "red" }}>{errorMessageEmail}</p>
                )}
                {emailExit && <p style={{ color: "red" }}>{emailExit}</p>}
              </div>
              <div className="col-12 mb-2">
                <div className="input-group ">
                  <span className="input-group-text" id="basic-addon1">
                    <RiLockPasswordFill />
                  </span>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter Your Password"
                    name="password"
                    disabled
                    value={resiter.password}
                    onChange={handleResiter}
                  />
                </div>
                <p style={{ color: "red", marginBottom: "2px" }}>
                  {errorValue.name}
                </p>
              </div>
              <NewTabSignUp
                data={data}
                count={count}
                tabChange={tabChange}
                resiter={resiter}
                handleResiter2={handleResiter2}
              />
              <div className="col-lg-6 col-md-6 col-sm-12 form-group mb-2">
                <div className="input-group">
                  <select
                    id="myState"
                    name="state_id"
                    value={selectedState}
                    onChange={handleStateChange}
                    className="form-select form-control"
                  >
                    <option value="" disabled hidden>
                      Select State{" "}
                    </option>
                    {stateOptions
                      .sort((a, b) => a.name.localeCompare(b.name))
                      .filter((state) =>
                        state.name
                          .toLowerCase()
                          .includes(searchQuery.toLowerCase()),
                      )
                      .map((state) => (
                        <option key={state.value} value={state.value}>
                          {state.name}
                        </option>
                      ))}
                  </select>
                </div>
                <span className="text-danger">{errorValue.state}</span>
              </div>
              {showNothing.includes(selectedUserType) ? null : (
                <>
                  {showBoth.includes(selectedUserType) && (
                    <>
                      <div className="col-lg-12 mb-2">
                        <div className="input-group">
                          <span className="input-group-text" id="basic-addon1">
                            <RiLockPasswordFill />
                          </span>
                          <input
                            type="text"
                            className="form-control"
                            placeholder="Enter Refer ID"
                            name="refer_id"
                            value={resiter.refer_id}
                            onChange={handleResiter}
                          />
                        </div>
                        <p style={{ color: "red", marginBottom: "2px" }}>
                          {errorValue.refer_id}
                        </p>
                      </div>
                      <div className="col-lg-12 mb-2">
                        <div
                          className="form-check form-check-form"
                          style={{ width: "100%" }}
                        >
                          <input
                            className="form-check-input ms-1"
                            type="checkbox"
                            id="flexCheckDefault"
                            checked={isChecked}
                            onChange={handleCheckboxChange}
                            disabled={isCheckboxDisabled}
                          />
                          <label
                            className="form-check-label"
                            htmlFor="flexCheckDefault"
                          >
                            Without ReferID
                          </label>
                        </div>
                      </div>
                    </>
                  )}
                  {showReferOnly.includes(selectedUserType) && (
                    <div className="col-lg-12 mb-2">
                      <div className="input-group">
                        <span className="input-group-text" id="basic-addon1">
                          <RiLockPasswordFill />
                        </span>
                        <input
                          type="text"
                          className="form-control"
                          placeholder="Enter Refer ID"
                          name="refer_id"
                          value={resiter.refer_id}
                          onChange={handleResiter}
                        />
                      </div>
                      <p style={{ color: "red", marginBottom: "2px" }}>
                        {errorValue.refer_id}
                      </p>
                    </div>
                  )}
                </>
              )}

              {["reseller partner api user", "Api User", "Reseller"].includes(
                selectedUserType,
              ) && (
                <div className="mb-2">
                  <input
                    className="form-control ms-1"
                    name="domain_name"
                    type="url"
                    placeholder="Domain Name"
                    pattern="https://.*"
                    title="URL must start with https://"
                    onChange={handleResiter}
                  />
                </div>
              )}
              <div>
                <button
                  type="button"
                  disabled={
                    !isNameValid(resiter.name) ||
                    !resiter.password ||
                    !isValidEmail(emailInitial.emailId) ||
                    initalValue.mobileNo.length !== 13 ||
                    errorMessageEmail ||
                    isReferRequirementFailed
                  }
                  className={`btn ${
                    !isNameValid(resiter.name) ||
                    !resiter.password ||
                    !isValidEmail(emailInitial.emailId) ||
                    initalValue.mobileNo.length !== 13 ||
                    errorMessageEmail ||
                    isReferRequirementFailed
                      ? "disableebuttoncls"
                      : "btn-login"
                  }`}
                  onClick={handleSubmit}
                >
                  {loader5 && (
                    <div
                      style={{ height: "16px", width: "16px" }}
                      className="spinner-border"
                      role="status"
                    >
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  )}
                  REGISTER
                </button>
              </div>
            </div>
          </form>
        ) : (
          <></>
        )}
        <Modal
          show={show}
          onHide={handleClose}
          aria-labelledby="contained-modal-title-vcenter"
          centered
          style={{
            zIndex: 1050,
          }}
        >
          <Modal.Header closeButton>
            <Modal.Title style={{ color: "green" }}>Thank You !</Modal.Title>
          </Modal.Header>
          <Modal.Body
            style={{
              backgroundColor: "#ffffff",
              transform: "translateZ(0)",
              backfaceVisibility: "hidden",
            }}
          >
            <div className="para-comor">
              <p>
                Thank you for choosing BankMudraa!.Our enterprise team will
                reach out to you shortly.
              </p>
              <p>
                If you have any further questions, feel free to contact us at{" "}
                <a href="mailto:support@bankMudraa.com" target="blank">
                  support@bankMudraa.com
                </a>{" "}
                .
              </p>
            </div>
          </Modal.Body>
        </Modal>
      </div>
      <div className="text-align-center mt-1">
        <div>
          Have an account?{" "}
          <Link
            to="/login-area"
            className="text-decoration-underline"
            style={{ color: "var(--bs-nav)" }}
          >
            Login
          </Link>{" "}
        </div>
      </div>
    </>
  );
}
export default SignUpMerchantForm;
