import { useState } from "react";
import SignUpMerchantForm from "./signUpMerchantForm/SignUpMerchantForm";
import { isMobileExits, sinupApi } from "../../../api/login/Login";
import { toast } from "react-toastify";
import Footer from "../../footer/footer";
import tranParentLogo from "../../../asesets/banner/transparentLogo.png";
import carousel1 from "../../../asesets/banner/login/1.png";
import carousel2 from "../../../asesets/banner/login/2.png";
import carousel3 from "../../../asesets/banner/login/3.png";
import carousel4 from "../../../asesets/banner/login/4.png";
import AnimatedWrapper from "../../../common/AnimateWrapper";
import Slider from "react-slick";

function SignUpMerchant() {
  const [loader1, setloader1] = useState(false);
  const [showMobileOtp, setShowMobileOtp] = useState(false);
  const [mobileExit, setMobileExit] = useState();
  const handleClose2 = () => setShowMobileOtp(false);
  const [mobileVeridedInput, setMobileVerified] = useState(true);
  const [mobileOtp, setMobileOtp] = useState(false);
  const [emailVeridedInput, setEmailVeridedInput] = useState(false);
  const [emailOtp, setEmailOtp] = useState(false);
  const [formResiter, setFormResiter] = useState(true);
  const [countryCode, setCountryCode] = useState();
  const [userIDD, setUserID] = useState();

  const [initalValue, setInitialValue] = useState({
    mobileNo: "+91",
    emailId: "",
  });
  const [veriFiedIconMobile, setVeriFiedIconMobile] = useState(false);

  const handleChange = (e) => {
  const clone = { ...initalValue };
  const input = e.target.value.replace(/[^0-9+]/g, ""); // allow only digits and '+'
  const name = e.target.name;

  let newValue = { ...initalValue };

  if (name === "mobileNo") {
    setMobileExit("");
    setVeriFiedIconMobile(false);
    if (input.length <= 13) newValue = { ...newValue, [name]: input };
    const mobileWithoutCode = input.replace("+91", "");
    if (mobileWithoutCode.length > 0) {
      const firstDigit = mobileWithoutCode[0];
      if (!["6", "7", "8", "9"].includes(firstDigit)) {
        setMobileExit("Enter valid Mobile Number.");
      } else if (mobileWithoutCode.length < 10) {
        setMobileExit("Enter 10 digit mobile number");
      } else if (mobileWithoutCode.length === 10) {
        setMobileExit("");
        isMobileExit(newValue.mobileNo);
      }
    } else {
      setMobileExit("Enter 10 digit mobile number");
    }
  } else {
    newValue = { ...newValue, [name]: input };
  }

  setInitialValue(newValue);
};

  const handleCountryCode = (e) => {
    setCountryCode(e.target.value);
    setInitialValue({ ...initalValue, mobileNo: e.target.value });
  };

  const isMobileExit = async (value) => {
    try {
      const res = await isMobileExits(value);
      if (res?.data?.isExist) setMobileExit("Mobile Number Already Exists");
      else {
        setMobileExit("");
        mobileGenerateOtpMobile(value);
        setShowMobileOtp(true);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const toastSuccessMessage = () => {
    toast.success(`OTP Sent Successfully.`, {
      position: "top-center",
      autoClose: 2000,
    });
  };

  const mobileGenerateOtpMobile = async (value) => {
    setloader1(true);
    try {
      const res = await sinupApi({ ...initalValue, mobileNo: value });
      setUserID(res?.data?.data?.user);
      if (res?.data?.statusCode === "200") {
        toastSuccessMessage();
        setMobileVerified(false);
        setMobileOtp(true);
      }
    } catch (error) {}
    setloader1(false);
  };

  const getOtp = () => mobileGenerateOtpMobile();

  const emailgetOtp = () => {
    setEmailOtp(true);
    setEmailVeridedInput(false);
  };

  const submitOtpEmail = () => {
    setFormResiter(true);
    setEmailVeridedInput(false);
    setEmailOtp(false);
  };

  const nextForm = () => {
    setFormResiter(true);
    setEmailVeridedInput(false);
  };

  const sliderSettings = {
    dots: true,
    infinite: true,
    autoplay: true,
    autoplaySpeed: 3500,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    fade: true,
  };

  const carouselImages = [carousel4, carousel1, carousel2, carousel3];

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        minHeight: "100dvh",
        fontFamily: "Inter, sans-serif",
      }}
    >
      <div
        style={{
          display: "flex",
          flex: 1,
          flexDirection: "row",
          background: "linear-gradient(135deg, #012a60 50%, #012a60 50%)",
          flexWrap: "wrap",
        }}
      >
        {/* LEFT INFO SECTION */}

        <div
          className="d-none d-md-flex"
          style={{
            flex: 1,
            minWidth: "300px",
            position: "relative",
            display: window.innerWidth < 768 ? "none" : "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            overflow: "hidden",
            background: "linear-gradient(135deg, #012a60, #012a60)",
            color: "#fff",
          }}
        >
          {/* <div
                    style={{
                      position: "absolute",
                      top: 30,
                      left: 30,
                      zIndex: 10,
                      display: "flex",
                      alignItems: "center",
                      gap: "10px",
                    }}
                  >
                    <img
                      src={tranParentLogo}
                      alt="Logo"
                      style={{ width: "80px", height: "80px" }}
                    />
                    <h1 style={{ fontSize: "24px", fontWeight: "bold" }}>
                      Merchant Portal
                    </h1>
                  </div> */}

          <Slider {...sliderSettings} style={{ width: "100%", height: "100%" }}>
            {carouselImages.map((img, i) => (
              <div key={i}>
                <img
                  src={img}
                  alt={`Slide ${i}`}
                  style={{
                    width: "100%",
                    height: "93vh",
                    objectFit: "cover",
                    // filter: "brightness(0.7)",
                  }}
                />
                {/* <div
                          style={{
                            position: "absolute",
                            top: "50%",
                            left: "50%",
                            transform: "translate(-50%, -50%)",
                            textAlign: "center",
                            color: "#fff",
                          }}
                        >
                          <h2 style={{ fontSize: "28px", fontWeight: "600" }}>
                            Fast & Secure Payments
                          </h2>
                          <p style={{ fontSize: "16px", marginTop: "8px" }}>
                            Experience seamless merchant onboarding and payouts.
                          </p>
                        </div> */}
              </div>
            ))}
          </Slider>
        </div>

        {/* RIGHT SIGNUP FORM SECTION */}
        <div
          style={{
            flex: 1,
            minWidth: "300px",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            background: "#012a60",
            color: "#fff",
            padding: "40px",
          }}
        >
          <div
            style={{
              backgroundColor: "rgba(255,255,255,0.1)",
              backdropFilter: "blur(10px)",
              borderRadius: "20px",
              width: "100%",
              maxWidth: "400px",
              padding: "30px 20px",
              boxShadow: "0 8px 24px rgba(0,0,0,0.2)",
            }}
          >
            <AnimatedWrapper>
              <h2
                style={{
                  textAlign: "center",
                  marginBottom: "20px",
                  color: "#fff",
                  fontSize: "24px",
                  letterSpacing: "1px",
                }}
              >
                Signup
              </h2>
              <SignUpMerchantForm
                mobileVeridedInput={mobileVeridedInput}
                getOtp={getOtp}
                mobileOtp={mobileOtp}
                emailVeridedInput={emailVeridedInput}
                emailgetOtp={emailgetOtp}
                nextForm={nextForm}
                emailOtp={emailOtp}
                submitOtpEmail={submitOtpEmail}
                formResiter={formResiter}
                setEmailVeridedInput={setEmailVeridedInput}
                setMobileOtp={setMobileOtp}
                setMobileVerified={setMobileVerified}
                initalValue={initalValue}
                handleChange={handleChange}
                mobileGenerateOtpMobile={mobileGenerateOtpMobile}
                userIDD={userIDD}
                handleCountryCode={handleCountryCode}
                countryCode={countryCode}
                setEmailOtp={setEmailOtp}
                setFormResiter={setFormResiter}
                loader1={loader1}
                showMobileOtp={showMobileOtp}
                handleClose2={handleClose2}
                mobileExit={mobileExit}
                setMobileExit={setMobileExit}
                veriFiedIconMobile={veriFiedIconMobile}
                setVeriFiedIconMobile={setVeriFiedIconMobile}
                setShowMobileOtp={setShowMobileOtp}
              />
            </AnimatedWrapper>
          </div>
        </div>
      </div>

      {/* FOOTER */}
      <div style={{ backgroundColor: "#012a60", color: "#fff" }}>
        <Footer notIsFixed={true} />
      </div>
    </div>
  );
}

export default SignUpMerchant;
